import { reducer as authReducer } from "./auth/index.tsx";
import { reducer as feedReducer } from "./feed/index.tsx";
import { connectRouter, history, routerMiddleware } from "./router.tsx";
import { applyMiddleware, combineReducers, createStore } from "redux";
import thunk from "redux-thunk";
var reducer = connectRouter(history)(combineReducers({
    auth: authReducer,
    feed: feedReducer
}));
var state = localStorage.getItem('state') ?
    JSON.parse(localStorage.getItem('state')) :
    {};
export var store = createStore(reducer, state, applyMiddleware(thunk, routerMiddleware(history)));
store.subscribe(function () {
    localStorage.setItem('state', JSON.stringify(store.getState()));
});
export default store;
