import { getQuery } from "../router.tsx";
export var getDataList = function (state) { return state.feed.data.list; };
export var getError = function (state) { return state.feed.error; };
export var getCategory = function (state, initialValue) {
    return getQuery(state).category || initialValue;
};
export var getId = function (state) {
    return getQuery(state).id;
};
